export const convertDate = (date) => {
    let newdate = new Date(date);
    return `${newdate.getFullYear()} 年 ${(newdate.getMonth() + 1).toString().padStart(2, '0')} 月 ${newdate.getDate()} 日`;
}

export const convertMGDate = (date) => {
    let newdate = new Date(date);
    return [
        (newdate.getFullYear() - 1911).toString(), '年',
        (newdate.getMonth() + 1).toString().padStart(2, '0'), '月',
        newdate.getDate().toString().padStart(2, '0'), '日'
    ];
}
