import { jsPDF } from "jspdf"

export const AddFont = async () =>{
    await fetch('../../kaiu-bold.json').then(async(result)=>{
        var font = (await result.json()).font;
        var callAddFont = function () {
            this.addFileToVFS('kaiu-bold.ttf', font);
            this.addFont('kaiu-bold.ttf', 'kaiu', 'bold');
        };
        jsPDF.API.events.push(['addFonts', callAddFont])
    })
};

