import React from 'react';
import { Link, useParams } from "react-router-dom";
import { ActivityInfo, GetAllJoin } from '../../services/user-service';
import { ExportStickerPDF, ExportListPDF } from '../../services/pdf-service';
import { convertDate } from '../../services/date-service';
import { NotificationManager } from 'react-notifications';
import { AddFont } from '../../fonts/kaiu-bold';
import { CheckBox } from '../../components/checkbox';
import { Loading } from '../../components/loading';


class ManageJoin extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            info: null,
            data: [],
            allSelect: false,
        }
    }

    componentDidMount() {
        ActivityInfo(this.props.id, (result) => {
            this.setState({ info: result })
        })

        GetAllJoin(
            this.props.id,
            (result) => {
                this.setState({ data: result, num: result.length })
            },
            (error) => {
                console.log(error);
            }
        )
    }

    async CheckBoxValueChange(prop, value) {
        let index = value.target.getAttribute('data-key');
        let copydata = this.state.data;
        copydata[index][prop] = !copydata[index][prop];

        const allCount = copydata.length;
        const selectCount = copydata.filter(e => e.check).length;
        const allSelectStatus = selectCount === 0 ? false : (selectCount === allCount ? true : "")

        await this.setState({ data: copydata, allSelect: allSelectStatus })
    }

    AllSelectChange = () => {
        const checkStatus = !this.state.allSelect;
        let copydata = this.state.data;
        copydata.forEach(e => e.check = checkStatus)

        this.setState({ allSelect: checkStatus, data: copydata })
    }

    dyTable = () => {
        let content = [];

        for (var i = 0; i < this.state.data.length; i++) {
            const userData = this.state.data[i];
            content.push(
                <div key={i} className="mt-3 mb-3 row" style={{ display: 'flex' }}>
                    <div className="col-1 pb-3 justify-content-center align-items-center text-nowrap">
                        <CheckBox data-key={i} checked={this.state.data[i].check || false} onChange={(check) => this.CheckBoxValueChange('check', check)} />
                    </div>
                    <div className="col-11 col-md-3 pb-3 justify-content-center align-items-center text-nowrap">
                        <label>姓名:{userData?.name}</label>
                    </div>
                    <div className="col-1 d-md-none"></div>
                    <div className="col-11 col-md-4 pb-3 justify-content-center align-items-center text-nowrap">
                        <label>出生年月日:{convertDate(userData.birthday)}</label>
                    </div>
                    <div className="col-1 d-md-none"></div>
                    <div className="col-11 col-md-4 pb-3 align-items-center text-nowrap">
                        <CheckBox className="me-3" disabled data-key={i} checked={this.state.data[i].light1} Text="太歲燈" />
                        <CheckBox disabled data-key={i} checked={this.state.data[i].light2} Text="光明燈" />
                    </div>
                </div>
            )
        }

        return content;
    }

    ExportStickerPDF = async () => {
        await AddFont();
        const light1s = this.state.data.filter(e => e.light1 && e.check);
        const light2s = this.state.data.filter(e => e.light2 && e.check);

        if (light1s.length === 0 && light2s.length === 0) {
            NotificationManager.warning('請選擇要列印的項目', '警告', 3000);
            return;
        }

        ExportStickerPDF(this.state.info.name, light1s, light2s);
    }

    ExportListPDF = async () => {
        await AddFont();
        const light1s = this.state.data.filter(e => e.light1);
        const light2s = this.state.data.filter(e => e.light2);

        if (light1s.length === 0 && light2s.length === 0) {
            NotificationManager.warning('沒有可列印的項目', '警告', 3000);
            return;
        }

        const mgYear=new Date(this.state.info.date).getFullYear()-1911;
        ExportListPDF(this.state.info.name, light1s, light2s, mgYear);
    }

    render() {
        if (!this.state.info) return (<Loading />)

        return (
            <div className="container">
                <div className="mx-auto shadow rounded mt-5 mb-5" style={{ maxWidth: '900px', width: '100%', padding: '40px 30px' }}>
                    <p className="fs-2 text-center">查看點燈資料</p>
                    <div className="row mb-4 py-3">
                        <div className="col-md-4 my-2">名稱:{this.state.info.name}</div>
                        <div className="col-md-5 my-2">截止日期:{convertDate(this.state.info.date)}</div>
                        <div className="col-md-3 my-2">總人數:{this.state.data.length}</div>
                        <div className="col-md-4 my-2">太歲燈:{this.state.data.filter(e => e.light1).length}</div>
                        <div className="col-md-4 my-2">光明燈:{this.state.data.filter(e => e.light2).length}</div>
                    </div>
                    <div className="row">
                        <div className="col-1 justify-content-center align-items-center text-nowrap">
                            <CheckBox checked={this.state.allSelect} onChange={this.AllSelectChange} />
                        </div>
                        <div className="col-3 justify-content-center align-items-center text-nowrap">
                            <label>全選</label>
                        </div>
                    </div>
                    <hr />
                    {this.dyTable()}
                    <div className="row mt-5 justify-content-center">
                        <div className="col-12 col-md-3 text-nowarp">
                            <button className="btn btn-primary w-100 mb-3" onClick={() => this.ExportStickerPDF()}>列印貼紙</button>
                        </div>
                        <div className="col-12 col-md-3">
                            <button className="btn btn-primary w-100 mb-3" onClick={() => this.ExportListPDF()}>列印清單</button>
                        </div>
                        <div className="col-12 col-md-3">
                            <Link className="btn btn-danger w-100 mb-3" to={'./../../../join'}>返回</Link>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}



export default function ManageJoinParams(props) {
    const { id } = useParams();

    return <ManageJoin id={id} />;
}