import { jsPDF } from "jspdf";
import { convertMGDate } from "./date-service";
import { LightConfig1, LightConfig2, lineBoldWidth, lineWidth, helfLineBoldWidth } from "../configs/pdf-config"

const lc1 = LightConfig1();
const lc2 = LightConfig2();

export const ExportStickerPDF = (filename, light1s, light2s) => {
    const doc = new jsPDF();

    doc.setTextColor(255, 0, 0);

    const last_x_index = 2;
    const last_y_index = 7;
    const start_x = 30;
    const start_y = 29;
    const move_x = 64;
    const move_y = 34.35;
    let x_index = 0;
    let y_index = 0;
    let x = start_x;
    let y = start_y;

    const printContent = (isLight1) => {
        return (e) => {
            const textLength = e.name.length;
            let deviation = 0;
            switch (textLength) {
                case 2:
                    deviation = 3;
                    break;
                case 4:
                    deviation = -3;
                    break;
                case 5:
                    deviation = -8;
                    break;
                case 6:
                    deviation = -12;
                    break;
                default:
                    break;
            }
            doc.setFont('kaiu', 'bold');
            doc.setFontSize(25);
            doc.text(e.name, x + deviation, y);

            if (e.birthday && isLight1) {
                setBirthdayFont(doc, x, y, convertMGDate(e.birthday))
            }

            if (x_index < last_x_index) {
                x_index++;
                x += move_x;
            } else {
                if (y_index < last_y_index) {
                    x_index = 0;
                    x = start_x;
                    y_index++;
                    y += move_y;
                } else {
                    doc.addPage();
                    x_index = 0;
                    y_index = 0;
                    x = start_x;
                    y = start_y;
                }
            }
        }
    }

    light1s.forEach(printContent(true))
    light2s.forEach(printContent(false))

    doc.save(`${filename}貼紙.pdf`);
}

export const ExportListPDF = (filename, light1s, light2s, mgYear) => {
    const doc = new jsPDF('l');

    doc.setTextColor(0, 0, 0);

    const lightCount1 = light1s.length;
    const lightCount2 = light2s.length;
    const lightNum1 = 20;
    const lightNum2 = 40;

    const lightPageTotal1 = Math.ceil(lightCount1 / lightNum1);
    const lightPageTotal2 = Math.ceil(lightCount2 / lightNum2);
    const maxPageTotal = Math.max(lightPageTotal1, lightPageTotal2);

    for (let pageNum = 0; pageNum < maxPageTotal; pageNum++) {
        if (pageNum !== 0) doc.addPage();
        ListPDFTemplate(doc, mgYear, pageNum + 1, maxPageTotal)

        for (let i = 0; i < lightNum1; i++) {
            const lightIndex1 = pageNum * lightNum1 + i;
            const userInfo = light1s[lightIndex1];
            if (userInfo) {
                //姓名
                doc.setFont('kaiu', 'bold');
                doc.setFontSize(18);
                const ColNameDev = (lc1.SecondColWidth - userInfo.name.length * 6) / 2;
                const RowDev = (i + 1) * lc1.LineHeight;
                doc.text(
                    userInfo.name,
                    lc1.SecondColX + ColNameDev,
                    lc1.SecondRowY + RowDev - 1.3
                )
                //出生
                const birthday = convertMGDate(userInfo.birthday);
                const birthdayLength = (birthday.join("").length - 3) * 3 + 3 * 6;
                const ColBirDev = (lc1.ThirdColWidth - birthdayLength) / 2;
                printListBirthday(
                    doc,
                    lc1.ThirdColX + ColBirDev,
                    lc1.SecondRowY + RowDev - 1.3,
                    birthday,
                )
            }
        }

        for (let i = 0; i < lightNum2; i++) {
            const lightIndex2 = pageNum * lightNum2 + i;
            const userInfo = light2s[lightIndex2];
            if (userInfo) {
                //姓名
                doc.setFont('kaiu', 'bold');
                doc.setFontSize(18);
                const ColNameX = i < 20 ? lc2.SecondColX : lc2.FourthColX;
                const ColNameWidth = i < 20 ? lc2.SecondColWidth : lc2.FourthColWidth;
                const ColNameDev = (ColNameWidth - userInfo.name.length * 6) / 2;
                const RowDev = (i % 20 + 1) * lc2.LineHeight;
                doc.text(
                    userInfo.name,
                    ColNameX + ColNameDev,
                    lc2.SecondRowY + RowDev - 1.3,
                )
            }
        }
    }

    doc.save(`${filename}清單.pdf`);
}

const ListPDFTemplate = (doc, year, pageNum, pageTotal) => {
    //print title
    doc.setFont('kaiu', 'bold');
    doc.setFontSize(30);
    doc.text("潮州天官宮信男、信女安太歲既平安燈名冊", 56, 20);

    ListLightTemplate1(doc, year, pageNum);
    ListLightTemplate2(doc, pageNum, pageTotal);
}

const ListLightTemplate1 = (doc, year, pageNum) => {
    doc.setLineWidth(lineBoldWidth);

    //畫外框
    doc.line(lc1.px1 - helfLineBoldWidth, lc1.py1, lc1.px2 + helfLineBoldWidth, lc1.py1);
    doc.line(lc1.px1, lc1.py1 - helfLineBoldWidth, lc1.px1, lc1.py2 + helfLineBoldWidth);
    doc.line(lc1.px1 - helfLineBoldWidth, lc1.py2, lc1.px2 + helfLineBoldWidth, lc1.py2);
    doc.line(lc1.px2, lc1.py1 - helfLineBoldWidth, lc1.px2, lc1.py2 + helfLineBoldWidth);

    //年度
    doc.setFont('kaiu', 'bold');
    doc.setFontSize(16);
    drowBold(() => doc.text("年份", lc1.px1, lc1.py1 - 1));
    drowBold(() => doc.text("年", lc1.px1 + 23, lc1.py1 - 1));
    doc.setFont('Times', 'bold');
    doc.text(`: ${year}`, lc1.px1 + 11, lc1.py1 - 1)

    //畫橫線
    for (let i = 1; i < lc1.LineCount; i++) {
        const y = lc1.LineHeight * i + lc1.py1;

        if (i == 1 || i == 2) {
            doc.setLineWidth(lineBoldWidth);
        } else {
            doc.setLineWidth(lineWidth);
        }

        doc.line(lc1.px1 - helfLineBoldWidth + 0.1, y, lc1.px2 + helfLineBoldWidth - 0.1, y);
    }

    //畫直線第二列
    doc.setLineWidth(lineBoldWidth);
    doc.line(
        lc1.SecondColX,
        lc1.FirstRowY - helfLineBoldWidth + 0.1,
        lc1.SecondColX,
        lc1.SecondRowY + helfLineBoldWidth - 0.1
    );
    doc.line(
        lc1.ThirdColX,
        lc1.FirstRowY - helfLineBoldWidth + 0.1,
        lc1.ThirdColX,
        lc1.SecondRowY + helfLineBoldWidth - 0.1
    );

    //畫直線二至最後一列
    doc.setLineWidth(lineWidth);
    doc.line(
        lc1.SecondColX,
        lc1.SecondRowY - helfLineBoldWidth + 0.1,
        lc1.SecondColX,
        lc1.py2 + helfLineBoldWidth - 0.1
    );
    doc.line(
        lc1.ThirdColX,
        lc1.SecondRowY - helfLineBoldWidth + 0.1,
        lc1.ThirdColX,
        lc1.py2 + helfLineBoldWidth - 0.1
    );

    //填字
    doc.setFont('kaiu', 'bold');
    doc.setFontSize(18);

    drowBold(() => doc.text("太歲燈名冊", lc1.px1 + lc1.TitDev, lc1.FirstRowY - 1.5));
    drowBold(() => doc.text("編號", lc1.FirstColX + lc1.FirstColDev, lc1.SecondRowY - 1.5));
    drowBold(() => doc.text("姓名", lc1.SecondColX + lc1.SecondColDev, lc1.SecondRowY - 1.5));
    drowBold(() => doc.text("出生", lc1.ThirdColX + lc1.ThirdColDev, lc1.SecondRowY - 1.5));
    drowBold(() => doc.text("年", lc1.ThirdColX + 13.7 + lc1.ThirdColDev, lc1.SecondRowY - 1.5));
    drowBold(() => doc.text("月", lc1.ThirdColX + 21.6 + lc1.ThirdColDev, lc1.SecondRowY - 1.5));
    drowBold(() => doc.text("日", lc1.ThirdColX + 29.5 + lc1.ThirdColDev, lc1.SecondRowY - 1.5));
    drowBold(() => doc.text("農曆", lc1.ThirdColX + 38 + lc1.ThirdColDev, lc1.SecondRowY - 1.5));
    doc.setFont('Times', 'bold');
    doc.text("(       )", lc1.ThirdColX + 36 + lc1.ThirdColDev, lc1.SecondRowY - 2)

    //編號
    const idCount = 20;
    doc.setFont('Times', 'bold');

    for (let i = 0; i < idCount; i++) {
        const index = i + (pageNum - 1) * idCount + 1;
        const indexStr = `${index}`;
        const lineHeightDeviation = (i + 1) * lc1.LineHeight;
        const colDeviation = (lc1.FirstColWidth - (indexStr.length * 3)) / 2;
        doc.text(indexStr, lc1.px1 + colDeviation, lc1.SecondRowY + lineHeightDeviation - 1);
    }
}

const ListLightTemplate2 = (doc, pageNum, pageTotal) => {
    doc.setLineWidth(lineBoldWidth);

    //畫外框
    doc.line(lc2.px1 - helfLineBoldWidth, lc2.py1, lc2.px2 + helfLineBoldWidth, lc2.py1);
    doc.line(lc2.px1, lc2.py1 - helfLineBoldWidth, lc2.px1, lc2.py2 + helfLineBoldWidth);
    doc.line(lc2.px1 - helfLineBoldWidth, lc2.py2, lc2.px2 + helfLineBoldWidth, lc2.py2);
    doc.line(lc2.px2, lc2.py1 - helfLineBoldWidth, lc2.px2, lc2.py2 + helfLineBoldWidth);

    //page
    doc.setFont('Times', 'bold');;
    doc.setFontSize(16);

    const pageStr = `${pageNum}/${pageTotal}`;
    doc.text(`page:${pageStr}`, lc2.px2 - (15 + (pageStr.length - 1) * 2.8), lc2.py1 - 1);

    //畫橫線
    for (let i = 1; i < lc2.LineCount; i++) {
        const y = lc2.LineHeight * i + lc2.py1;

        if (i == 1 || i == 2) {
            doc.setLineWidth(lineBoldWidth);
        } else {
            doc.setLineWidth(lineWidth);
        }

        doc.line(lc2.px1 - helfLineBoldWidth + 0.1, y, lc2.px2 + helfLineBoldWidth - 0.1, y);
    }

    //畫直線第二列
    doc.setLineWidth(lineBoldWidth);
    doc.line(
        lc2.SecondColX,
        lc2.FirstRowY - helfLineBoldWidth + 0.1,
        lc2.SecondColX,
        lc2.SecondRowY + helfLineBoldWidth - 0.1
    );
    doc.line(
        lc2.ThirdColX,
        lc2.FirstRowY - helfLineBoldWidth + 0.1,
        lc2.ThirdColX,
        lc2.SecondRowY + helfLineBoldWidth - 0.1
    );
    doc.line(
        lc2.FourthColX,
        lc2.FirstRowY - helfLineBoldWidth + 0.1,
        lc2.FourthColX,
        lc2.SecondRowY + helfLineBoldWidth - 0.1
    );

    //畫直線二至最後一列
    doc.setLineWidth(lineWidth);
    doc.line(
        lc2.SecondColX,
        lc2.SecondRowY - helfLineBoldWidth + 0.1,
        lc2.SecondColX,
        lc2.py2 + helfLineBoldWidth - 0.1
    );
    doc.line(
        lc2.ThirdColX,
        lc2.SecondRowY - helfLineBoldWidth + 0.1,
        lc2.ThirdColX,
        lc2.py2 + helfLineBoldWidth - 0.1
    );
    doc.line(
        lc2.FourthColX,
        lc2.SecondRowY - helfLineBoldWidth + 0.1,
        lc2.FourthColX,
        lc2.py2 + helfLineBoldWidth - 0.1
    );

    //填字
    doc.setFont('kaiu', 'bold');
    doc.setFontSize(18);

    drowBold(() => doc.text("光明燈名冊", lc2.px1 + lc2.TitDev, lc2.FirstRowY - 1.5));
    drowBold(() => doc.text("編號", lc2.FirstColX + lc2.FirstColDev, lc2.SecondRowY - 1.5));
    drowBold(() => doc.text("姓名", lc2.SecondColX + lc2.SecondColDev, lc2.SecondRowY - 1.5));
    drowBold(() => doc.text("編號", lc2.ThirdColX + lc2.ThirdColDev, lc2.SecondRowY - 1.5));
    drowBold(() => doc.text("姓名", lc2.FourthColX + lc2.FourthColDev, lc2.SecondRowY - 1.5));

    //編號
    const idCount = 40;
    doc.setFont('Times', 'bold');

    for (let i = 0; i < idCount; i++) {
        const index = i + (pageNum - 1) * idCount + 1;
        const indexStr = `${index}`;
        const lineHeightDeviation = (i % 20 + 1) * lc2.LineHeight;
        const colX = i < 20 ? lc2.FirstColX : lc2.ThirdColX;
        const colWidth = i < 20 ? lc2.FirstColWidth : lc2.ThirdColWidth;
        const colDeviation = (colWidth - (indexStr.length * 3)) / 2;
        doc.text(indexStr, colX + colDeviation, lc2.SecondRowY + lineHeightDeviation - 1);
    }
}

const drowBold = (callback) => {
    callback();
    callback();
}

const setBirthdayFont = (doc, x, y, font) => {
    doc.setFontSize(10);
    font.forEach((e, i) => {
        if (i % 2 === 0) {
            doc.setFont('Times', 'bold');
        } else {
            doc.setFont('kaiu', 'bold');
        }

        if (i !== 0) {
            x += 4.2;
        }

        if (i === 0) {
            ///民國年長度判斷前移
            switch (font[0].length) {
                case 3:
                    x -= 1;
                    break;
                default:
                    break;
            }
        }

        doc.text(e, x, y + 4);

        if (i === 0) {
            ///民國年長度判斷後移
            switch (font[0].length) {
                case 3:
                    x += 2;
                    break;
                default:
                    break;
            }
        }
    })
}


const printListBirthday = (doc, x, y, font) => {
    font.forEach((e, i) => {
        if (i % 2 === 0) {
            doc.setFont('Times', 'bold');
        } else {
            doc.setFont('kaiu', 'bold');
        }

        doc.text(e, x, y);

        if (i % 2 === 0) {
            x += e.length * 3;
        } else {
            x += e.length * 6;
        }
    })
}