export const lineBoldWidth = 0.7;
export const lineWidth = 0.5;
export const helfLineBoldWidth = lineBoldWidth / 2;

export const LightConfig1 = () => {
    const px1 = 20;
    const py1 = 35;
    const px2 = 140;
    const py2 = 192;
    const LineCount = 22;
    const LineHeight = (py2 - py1) / LineCount;
    const FirstColWidth = 20;
    const SecondColWidth = 40;
    const ThirdColWidth = px2 - px1 - FirstColWidth - SecondColWidth;
    const FirstRowY = py1 + LineHeight;
    const SecondRowY = FirstRowY + LineHeight;
    const FirstColX = px1;
    const SecondColX = px1 + FirstColWidth;
    const ThirdColX = px1 + FirstColWidth + SecondColWidth;
    const TitDev = (px2 - px1 - 29) / 2;
    const FirstColDev = (FirstColWidth - 12) / 2;
    const SecondColDev = (SecondColWidth - 13) / 2;
    const ThirdColDev = (ThirdColWidth - 52) / 2;

    return {
        px1,
        py1,
        px2,
        py2,
        LineCount,
        LineHeight,
        FirstColWidth,
        SecondColWidth,
        ThirdColWidth,
        FirstRowY,
        SecondRowY,
        FirstColX,
        SecondColX,
        ThirdColX,
        TitDev,
        FirstColDev,
        SecondColDev,
        ThirdColDev,
    };
}


