import {
  getDatabase,
  ref,
  set,
  get
} from 'firebase/database';
import {
  canActivate,
  getAsync
} from './firebase-async';
import {
  getAuth
} from "firebase/auth";



const db = getDatabase();
const auth = getAuth();

export const AllGeneralUser = async (callback) => {
  let isActives = {};
  let isAdmins = {};
  let isSuperAdmins = {};

  await getAsync(ref(db, "isActive")).then((snapshot) => {
    if (snapshot.exists() && (typeof snapshot.val()) === "object") {
      isActives = snapshot.val();
    }
  })

  await getAsync(ref(db, "isAdmin")).then((snapshot) => {
    if (snapshot.exists() && (typeof snapshot.val()) === "object") {
      isAdmins = snapshot.val();
    }
  })

  await getAsync(ref(db, "isSuperAdmin")).then((snapshot) => {
    if (snapshot.exists() && (typeof snapshot.val()) === "object") {
      isSuperAdmins = snapshot.val();
    }
  })

  await getAsync(ref(db, `users`)).then((snapshot) => {
    if (snapshot.exists()) {
      const userData = snapshot.val();
      const userIds = Object.keys(userData);
      let userList = [];

      for (let i = 0; i < userIds.length; i++) {

        const oldData = userData[userIds[i]];
        if (isSuperAdmins[userIds[i]] || isAdmins[userIds[i]]) continue;

        let newData = {
          Id: userIds[i],
          email: oldData.email || false,
          isActive: isActives[userIds[i]] || false,
          isAdmin: isAdmins[userIds[i]] || false,
          isChange: false,
        }

        userList.push(newData);
      }

      callback(userList);
    } else {
      callback([])
    }
  }).catch((error) => {
    console.error(error);
  });
}

