export const UpdateAllGeneralUserAuth = (users, callback, errorCallback) => {
  try {
    for (let i = 0; i < users.length; i++) {
      const oldData = users[i];
      if (oldData.isChange) set(ref(db, `isActive/${oldData.Id}`), oldData.isActive)
    }
    callback();
  } catch (error) {
    errorCallback(error)
  }
}

export const UpdateAllUserAuth = (users, callback, errorCallback) => {
  try {
    for (let i = 0; i < users.length; i++) {
      const oldData = users[i];
      if (oldData.isChange) {
        set(ref(db, `isActive/${oldData.Id}`), oldData.isActive)
        set(ref(db, `isAdmin/${oldData.Id}`), oldData.isAdmin)
      }
    }
    callback();
  } catch (error) {
    errorCallback(error)
  }
}

export const AddActivity = (id, data, callback, errorCallback) => {
  set(ref(db, `activity/${id}`), {...data,time:new Date().getTime()}).then(_ => callback()).catch(_ => errorCallback());
}

export const EditActivity = (id, data, callback, errorCallback) => {
  set(ref(db, `activity/${id}`), {...data,time:new Date().getTime()}).then(_ => callback()).catch(errorCallback);
}

export const ActivityCount = (callback) => {
  get(ref(db, `activity/`)).then((snapshot) => {
    if (snapshot.exists()) {
      callback(Object.keys(snapshot.val()).length);
    } else {
      callback(0)
    }
  }).catch((error) => {
    console.error(error);
  });
}

export const JoinActivity = async (callback) => {
  await canActivate();
  getAsync(ref(db, `activity`)).then((snapshot) => {
    if (snapshot.exists()) {
      const originData = snapshot.val().map((e, index) => ({ ...e, date: TimeToISOString(e.date), id: index }));
      const filterSortData = originData.filter(e => new Date(Today()) <= new Date(e.date)).sort((a, b) => a.date < b.date ? 1 : -1);
      callback(filterSortData);
    } else {
      callback([]);
    }
  }).catch((error) => {
    console.error(error);
  });
}

