export const ActivityInfo = (id, callback) => {
  getAsync(ref(db, `activity/${id}`)).then((snapshot) => {
    if (snapshot.exists()) {
      const data=snapshot.val();
      callback({
        name:data.name,
        date:new Date(data.date).toISOString().split("T")[0],
      });
    } else {
      callback([]);
    }
  }).catch((error) => {
    console.error(error);
  });
}

export const GetJoin = async (id, callback, errorCallback) => {
  await canActivate();

  const currentUser = auth.currentUser;
  getAsync(ref(db, `join/${id}/${currentUser.uid}`)).then((snapshot) => {
    if (snapshot.exists()) {
      callback(snapshot.val());
    } else {
      callback(
        [{
          name: "",
          birthday: "",
          light1: false,
          light2: false
        },]
      );
    }
  }).catch((error) => {
    errorCallback(error)
  });
}

export const SentJoin = (id, data, callback, errorCallback) => {
  const currentUser = auth.currentUser;
  set(ref(db, `join/${id}/${currentUser.uid}`), data).then(_ => callback()).catch(_ => errorCallback());
}


export const GetAllJoin = async (id, callback, errorCallback) => {
  await canActivate();

  getAsync(ref(db, `join/${id}`)).then((snapshot) => {
    if (snapshot.exists()) {
      const allOldData = snapshot.val();
      const dataKey = Object.keys(allOldData);

      let allNewData = [];
      for (let i = 0; i < dataKey.length; i++) {
        allNewData = allNewData.concat(allOldData[dataKey[i]]);
      }

      callback(allNewData);
    } else {
      callback(
        []
      );
    }
  }).catch((error) => {
    errorCallback(error)
  });
}


export const SaveTemplate = async (data, callback, errorCallback) => {
  await canActivate();
  const currentUser = auth.currentUser;
  set(ref(db, `template/${currentUser.uid}`), data).then(_ => callback()).catch(_ => errorCallback());
}

export const LoadTemplate = async (callback, errorCallback) => {
  await canActivate();
  const currentUser = auth.currentUser;
  getAsync(ref(db, `template/${currentUser.uid}`)).then((snapshot) => {
    if (snapshot.exists()) {
      callback(snapshot.val());
    } else {
      throw new Error('nodata');
    }
  }).catch((error) => {
    errorCallback(error)
  });
}

export const Today = () => {
  let today = new Date();
  return `${today.getFullYear()}-${(today.getMonth() + 1).toString().padStart(2, '0')}-${today.getDate()}`;
}

export const TimeToISOString = (time) => {
  return new Date(time).toISOString().split("T")[0]
}