import { useRef } from "react"

export function CheckBox(props){
    const checkbox=useRef(null)

    if(checkbox.current){
        if(props.checked===""){
            checkbox.current['indeterminate']=true;
        }else{
            checkbox.current['indeterminate']=false;
        }
    }

    let textLabel=null;
    if(props.Text){
        textLabel= <label className="form-check-label">
                        {props.Text}
                    </label>;
    }

    return (
        <div className={`form-check d-inline-block${props.className?` ${props.className}`:""}`}>
            <input ref={checkbox} disabled={props.disabled} className="form-check-input" type="checkbox" data-key={props["data-key"]} checked={props.checked} onChange={props.onChange?props.onChange:()=>{}} />
            {textLabel}
        </div>
    )
}